@charset "utf-8";




:root {
  --black: #333132;
  --stroke: 4px solid var(--black);
  --stroke-half: 2px solid var(--black);

  /* 폰트 */
  --font-title-bold:'GangwonEduPowerExtraBoldA';


  --under-color:darksalmon;

  --color-01: wheat;
  --color-02: antiquewhite;
  --color-03: burlywood;
  --color-04: blanchedalmond;
  --color-05: lightsalmon;
  --color-06: cornsilk;
  --color-07: bisque;
  --color-08: beige;
  --color-09: cadetblue;
  --color-10: khaki;
  --color-11: darkseagreen;


}




body{
  cursor: url("../../public/images/cursor.png"), auto;
  position:absolute;
  width:100%;
  height:100%;

  font-family: "Pretendard Variable";
    background-color: beige;
} 

a:hover {
  cursor: url("../../public/images/cursor_hover.png"), auto;
 }
button:hover {
  cursor: url("../../public/images/cursor_hover.png"), auto;
 }



 /* 드래그 색상 */
 ::selection {
  background-color: var(--color-05);
  color:var(--black);
  font-weight: bold;
}

/* 런드리 볼드- TTLaundryGothicB */
@font-face {
  font-family: 'TTLaundryGothicB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/2403-2@1.0/TTLaundryGothicB.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;

}
/* 강원교육튼튼체 */
@font-face {
  font-family: 'GangwonEduPowerExtraBoldA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEduPowerExtraBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

/* MBC 1961 굴림 */
@font-face {
  font-family: 'MBC1961GulimM';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-01@1.0/MBC1961GulimM.woff2') format('woff2');
